import { Container, makeStyles, Grid, styled } from "@material-ui/core";
import { Button } from "@material-ui/core";
import { Twitter, Instagram } from "@material-ui/icons";
import AliceCarousel from "react-alice-carousel";
import "react-alice-carousel/lib/alice-carousel.css";
import "./works.scss";

const useStyles = makeStyles((theme) => ({
  container: {
    paddingTop: theme.spacing(10),
  },
}));

const handleDragStart = (e) => e.preventDefault();

const items = [
  <img
    src="assets/1.png"
    onDragStart={handleDragStart}
    className="carousel-img"
    loading="lazy"
  />,
  <img
    src="assets/2.png"
    onDragStart={handleDragStart}
    className="carousel-img"
    loading="lazy"
  />,
  <img
    src="assets/3.png"
    onDragStart={handleDragStart}
    className="carousel-img"
    loading="lazy"
  />,
  <img
    src="assets/4.png"
    onDragStart={handleDragStart}
    className="carousel-img"
    loading="lazy"
  />,
  <img
    src="assets/5.png"
    onDragStart={handleDragStart}
    className="carousel-img"
    loading="lazy"
  />,
  <img
    src="assets/6.png"
    onDragStart={handleDragStart}
    className="carousel-img"
    loading="lazy"
  />,

  <img
    src="assets/1.png"
    onDragStart={handleDragStart}
    className="carousel-img"
  />,
  <img
    src="assets/2.png"
    onDragStart={handleDragStart}
    className="carousel-img"
  />,
  <img
    src="assets/3.png"
    onDragStart={handleDragStart}
    className="carousel-img"
  />,
  <img
    src="assets/4.png"
    onDragStart={handleDragStart}
    className="carousel-img"
  />,
  <img
    src="assets/5.png"
    onDragStart={handleDragStart}
    className="carousel-img"
  />,
  <img
    src="assets/6.png"
    onDragStart={handleDragStart}
    className="carousel-img"
  />,
];

const responsive = {
  0: {
    items: 3,
  },
  512: {
    items: 5,
  },
  1024: {
    items: 7,
  },
};

const Works = () => {
  const classes = useStyles();
  return (
    <Container className={classes.container} container id="works">
      <Grid>
        <h1>More works by Jumaker </h1>

        <AliceCarousel
          infinite
          disableDotsControls
          responsive={responsive}
          autoPlay
          mouseTracking
          items={items}
        />
      </Grid>
      <a href="#" className="works-link">
        <h4>View all artworks by Jumaker</h4>
      </a>
    </Container>
  );
};

export default Works;
