import { Container, makeStyles, Grid, styled } from "@material-ui/core";
import { Button } from "@material-ui/core";
import { Twitter, Instagram } from "@material-ui/icons";
import "./artist.scss";

const useStyles = makeStyles((theme) => ({
  container: {
    paddingTop: theme.spacing(10),
  },
}));

const Artisi = () => {
  const classes = useStyles();
  return (
    <Container className={classes.container}>
      <Grid container id="artist">
        <Grid center item sm={6} xs={10} data-aos="fade-left">
          <h1>History</h1>

          <p className="created-art">Created by @Jumaker</p>

          <p className="date-created">One day ago [view tx]</p>
        </Grid>

        <Grid center item sm={6} xs={10} data-aos="fade-right">
          <h1>Artist</h1>
          <div className="artist-photo">
            <img src="assets/artist.png" alt="artist" />
            <div>
              <p className="created-art">@Jumaker</p>
              <p className="date-created">United Arab Emirates</p>
            </div>
          </div>
          <div className="artist-socials">
            <p>
              <a href="#" target="_blank" rel="noopener noreferrer">
                <Instagram className="icon-class" /> @Jumaker
              </a>
            </p>
            <p>
              <a href="#" target="_blank" rel="noopener noreferrer">
                <Twitter className="icon-class" /> @Jumaker
              </a>
            </p>
          </div>
        </Grid>
      </Grid>
    </Container>
  );
};

export default Artisi;
