import React from "react";
import "./footer.scss";
import { Container, makeStyles, Grid, styled } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  container: {
    paddingTop: theme.spacing(10),
  },
}));
const Footer = () => {
  const classes = useStyles();
  return (
    <>
      <div id="footer">
        <Container className={classes.container}>
          <Grid container>
            <div className="top-title">
              <h1 className="top-title-1">Are you an Artist?</h1>
              <h1 className="top-title-2">Tell us about you</h1>
            </div>
            <Grid container>
              <Grid center item md={6} xs={10} className="footer-sec-section">
                <h3>hello@jumy.co</h3>
                <p>
                  <span>The Offices 4, One Central</span>
                  <span>Dubai, United Arab Emirates</span>
                </p>
                <p>All right reserved.</p>
              </Grid>

              <Grid center item md={6} xs={10} className="second-footer">
                <div className="footer-social">
                  <a href="#" target="_blank">
                    Instagram
                  </a>
                  <a href="#" target="_blank">
                    Twitter
                  </a>
                  <a href="#" target="_blank">
                    Discord
                  </a>
                </div>
                <div className="footer-social">
                  <a href="#" target="_blank">
                    Terms
                  </a>
                  <a href="#" target="_blank">
                    FAQ
                  </a>
                  <a href="#" target="_blank">
                    Policy
                  </a>
                </div>
              </Grid>
            </Grid>
          </Grid>
        </Container>
      </div>
    </>
  );
};

export default Footer;
